<template>
  <div class="navbar">

    <div class="navbarLogo">
      <img src="@/assets/logo.png" class="navbarLogoImg" alt="">
      <h1 class="navbarLogoTitle">SYRIAN WORKER</h1>
    </div>

    <div class="navbarContainer">

      <div class="navbarContainerDiv">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="navbarContainerDivSvg"
        @click="hideLinks = !hideLinks" v-on:keyup.esc="hideLinks = true">
          <path d="M1 1h18v2H1V1zm0 8h18v2H1V9zm0 8h18v2H1v-2zM1 5h18v2H1V5zm0 8h18v2H1v-2z"/>
        </svg>
      </div>

      <!-- click button for cancelling or hiding links div in sm width -->
      <button v-if='!hideLinks' @click="hideLinks = true" v-on:keyup.esc="hideLinks = true"
      class="navbarContainerButton"></button>

      <div class="navbarContainerLinks"
      :class="{ 'navbarContainerLinksView' : hideLinks }" @click="hideLinks = !hideLinks">

        <div class="navbarLinksTextDiv">
          <router-link :to="{ name: 'Home' }"> </router-link>
          <div class="navbarLinksTextContainer" v-if="!isAuth">
            <p @click="scroll('feature')" class="navbarLinksText"> </p>
            <p @click="scroll('video')" class="navbarLinksText"> </p>
            <p @click="scroll('faq')" class="navbarLinksText"> </p>
            <p @click="scroll('contact')" class="navbarLinksText"> </p>
          </div>
          <div class="navbarLinksTextContainer" v-if="isAuth">
            <router-link class="navbarLinksText" :to="{ name: 'profile' }"> </router-link>
            <router-link class="navbarLinksText" :to="{ name: 'work' }"> </router-link>
          </div>
        </div>

        <hr class="navbarHr">

        <div class="navbarlinksSpecial">
          <router-link :to="{ name: 'signup' }" class="navbarlinksSpecialText" v-if="!isAuth">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="navbarlinksSpecialSvg">
              <path d="M0 0h48v48H0z" fill="none"/><path d="M28 4H12C9.79 4 8.02 5.79 8.02 8L8 40c0 2.21 1.77
              4 3.98 4H36c2.21 0 4-1.79 4-4V16L28 4zm4 28h-6v6h-4v-6h-6v-4h6v-6h4v6h6v4zm-6-14V7l11 11H26z"/>
            </svg>
             
          </router-link>
          <router-link :to="{ name: 'login' }"  class="navbarlinksSpecialText" v-if="!isAuth">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="navbarlinksSpecialSvg">
              <path d="M5 5a5 5 0 0 1 10 0v2A5 5 0 0 1 5 7V5zM0 16.68A19.9 19.9 0 0 1 10 14c3.64
              0 7.06.97 10 2.68V20H0v-3.32z"/></svg>
             
          </router-link>
          <router-link :to="{ name: 'profile' }"  class="navbarlinksSpecialText" v-if="isAuth">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="navbarlinksSpecialSvg">
              <path d="M5 5a5 5 0 0 1 10 0v2A5 5 0 0 1 5 7V5zM0 16.68A19.9 19.9 0 0 1 10 14c3.64
              0 7.06.97 10 2.68V20H0v-3.32z"/></svg>
            {{ getuserName }}
          </router-link>
          <p class="navbarlinksSpecialText" v-if="isAuth" @click="logout" >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="navbarlinksSpecialSvg">
              <path d="M4 10c0-1.1.9-2 2-2h8c1.1 0 2 .9 2 2v8c0 1.1-.9 2-2 2H6c-1.1 0-2-.9-2-2v-8zm2
              0v8h8v-8h-2V8H8v2H6zm3-6.17V16h2V3.83l3.07 3.07 1.42-1.41L10 0l-.7.7L4.5 5.5l1.42 1.4L9 3.84z"/></svg>
             
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      hideLinks: true,
    }
  },

  async mounted () {
    await this.$store.commit('Set_Auth')
    if (this.$store.getters['getIsAuth']) { await this.$store.dispatch('getData')}
  },

  methods: {
    async scroll (name) {
      await this.$router.push({ name: 'Home', path: '/' }).catch(()=>{})
      document.getElementById(name).scrollIntoView()
    },

    async logout () {
      try {
        localStorage.removeItem('csrf')
        this.$store.commit('Set_Auth')
        location.reload()
      } catch (error) {
        console.log(error)
      }
    }
  },

  computed: {
    isAuth () {
      return this.$store.getters['getIsAuth']
    },

    getuserName () {
      const user = this.$store.getters['getUser']
      if (user) {
        return user.username
      } else {
        return 'error'
      }
    }
  }
}
</script>
