<template>
  <div class="action">

    <navbar class='appNavbar' />

    <!-- content -->
    <div class="actionDiv">

      <alertSuccess v-if="showAlertSuccess"
      title= "      ..."
      content= "           "
       />

      <alertError v-if="showAlertError"
      title= "    ..."
      :moreInfo= "serverErrorsMassage"
      />

      <!-- reset Div -->
      <div class="actionContainer">

        <!-- title -->
        <h1 class="actionTitle">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="actionSvg">
              <path :d="resetSvg"/>
          </svg>  
        </h1>

          <!-- info -->
          <ValidationObserver v-slot="{ invalid }" class="w-full">
            <form @submit.prevent="reset" class="form">

              <!-- email -->
              <ValidationProvider rules="required|email"
              v-slot="{ errors }" class="formField">
                <div class="field">
                  <label class="fieldLabel"></label>
                  <input type="email" class="fieldInput" v-model.trim="email" placeholder="mohammed@gmail.com"/>
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                    <path :d="emailSvg"/>
                  </svg>
                </div>
                <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </ValidationProvider>

              <vue-recaptcha sitekey="6LfsVsQZAAAAAM-_U-Zxk0GwROnmx07HhycfsQgh" ref="recaptcha"
              size="invisible"  @verify="onCaptchaVerified" @expired="onCaptchaExpired" :loadRecaptchaScript="true">
              </vue-recaptcha>

              <button class="formButton" type="submit" :disabled="invalid">
                <svg class="formButtonSvg" viewBox="0 0 58 58" xmlns="http://www.w3.org/2000/svg" v-if="isLaoding">
                  <waitIcon />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="formButtonSvg" v-if="!isLaoding">
                  <path :d="okSvg" />
                </svg>
              </button>
            </form>
          </ValidationObserver>
      </div>
    </div>

    <footerDiv />
    <bottomDiv />

  </div>
</template>

<script>
import VueRecaptcha from 'vue-recaptcha'
import navbar from '@/components/navbar.vue'
import waitIcon from '@/components/waitIcon.vue'
import footerDiv from '@/components/footerDiv.vue'
import bottomDiv from '@/components/bottomDiv.vue'
import alertError from '@/components/alertError.vue'
import alertSuccess from '@/components/alertSuccess.vue'
import { ValidationProvider, ValidationObserver, extend, setInteractionMode } from 'vee-validate'
import { required, email } from 'vee-validate/dist/rules'
import { RESET } from '@/assets/graphql/auth'
import { RESETICON, EMAIL, OK } from '@/assets/icons.js'

setInteractionMode('aggressive')

extend('required', { ...required, message: '  ' })
extend('email', { ...email, message: '    ' })

export default {
  components: { VueRecaptcha, waitIcon, ValidationProvider, ValidationObserver, alertError,
    alertSuccess, navbar, footerDiv, bottomDiv },

  data () {
    return {
      email: '',
      showAlertError: false,
      showAlertSuccess: false,
      serverErrorsMassage: '',
      isLaoding: false,
      emailSvg: EMAIL,
      resetSvg: RESETICON,
      okSvg: OK
    }
  },

  created () {
    window.scrollTo(0, 0)
  },

  methods: {
    reset () {
      this.isLaoding = true
      this.$refs.recaptcha.execute()
    },

    onCaptchaVerified (recaptchaToken) {
      const self = this
      self.status = "submitting"
      self.$refs.recaptcha.reset()
      this.$apollo.query({
        query: RESET,
        fetchPolicy: 'network-only',
        variables: {
          email: this.email,
          recaptchaToken: recaptchaToken
        }
      }).then((data) => {
        if (data.data.reset.status) {
          this.isLaoding = false
          window.scrollTo(0, 0)
          this.showAlertError = false
          this.showAlertSuccess = true
        }
        else if (!data.data.reset.status) {
          this.isLaoding = false
          window.scrollTo(0, 0)
          this.serverErrorsMassage = data.data.reset.massage
          this.showAlertError = true
          this.showAlertSuccess = false
        }
      }).catch((error) => {
        console.log(error)
      })
    },

    onCaptchaExpired () {
      this.$refs.recaptcha.reset();
    }

  }
}
</script>

<style>
button:disabled,
button[disabled]{
  cursor:not-allowed !important;
}
</style>
