<template>
  <div class="action">

    <navbar class='appNavbar' />

    <!-- content -->
    <div class="actionDiv">

      <alertSuccess v-if="showAlertSuccess"
      title= "     ..."
      content= "      "
       />

      <alertError v-if="showAlertError"
      title= "    ..."
      :moreInfo= "serverErrorsMassage"
      />

      <!-- reset account password -->
      <div class="actionContainer">

        <!-- title -->
        <h1 class="actionTitle">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="actionSvg">
            <path :d="resetSvg" />
        </svg>  
        </h1>

          <!-- info -->
          <ValidationObserver v-slot="{ invalid }" class="w-full">
            <form @submit.prevent="resetPassword" class="form">

              <!-- password -->
              <ValidationObserver class="formField">
                <ValidationProvider rules="required|min_7:7|max_15:15" vid="password" v-slot="{ errors }" class="formField">
                  <div class="field">
                    <label class="fieldLabel"> </label>
                    <input type="password" class="fieldInput" ref="password" v-model.trim="password" placeholder="moh_10_1_2000" />
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                      <path :d="passwordSvg" />
                    </svg>
                  </div>
                  <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
                </ValidationProvider>
                <ValidationProvider rules="required|confirmed:password" v-slot="{ errors }" class="formField">
                  <div class="field">
                    <label class="fieldLabel">  </label>
                    <input type="password" class="fieldInput" v-model.trim="passwordConfirm" placeholder="moh_10_1_2000" />
                      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                        <path :d="passwordSvg"/>
                    </svg>
                  </div>
                  <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
                </ValidationProvider>
              </ValidationObserver>

              <vue-recaptcha sitekey="6LfsVsQZAAAAAM-_U-Zxk0GwROnmx07HhycfsQgh" ref="recaptcha"
              size="invisible"  @verify="onCaptchaVerified" @expired="onCaptchaExpired" :loadRecaptchaScript="true">
              </vue-recaptcha>

              <button class="formButton" type="submit" :disabled="invalid">
                <svg class="formButtonSvg" viewBox="0 0 58 58" xmlns="http://www.w3.org/2000/svg" v-if="isLaoding">
                  <waitIcon />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="formButtonSvg" v-if="!isLaoding">
                  <path :d="okSvg" />
                </svg>
              </button>
            </form>
          </ValidationObserver>
        </div>
      </div>
    <footerDiv />
    <bottomDiv />
  </div>
</template>

<script>
import VueRecaptcha from 'vue-recaptcha'
import navbar from '@/components/navbar.vue'
import waitIcon from '@/components/waitIcon.vue'
import footerDiv from '@/components/footerDiv.vue'
import bottomDiv from '@/components/bottomDiv.vue'
import alertError from '@/components/alertError.vue'
import alertSuccess from '@/components/alertSuccess.vue'
import { ValidationProvider, ValidationObserver, extend, setInteractionMode } from 'vee-validate'
import { required, min, max, confirmed } from 'vee-validate/dist/rules'
import { RESET_VERIFY } from '@/assets/graphql/auth'
import { RESETICON, PASSWORD, OK } from '@/assets/icons.js'

setInteractionMode('aggressive')

extend('required', { ...required, message: '  ' })
extend('min_7', { ...min, message: '       7' })
extend('max_15', { ...max, message: '       15' })
extend('confirmed', { ...confirmed, message: '   ' })

export default {
  components: { VueRecaptcha, ValidationProvider, waitIcon, ValidationObserver, alertError,
    alertSuccess, navbar, footerDiv, bottomDiv },

  data () {
    return {
      password: '',
      passwordConfirm: '',
      verifyToken: this.$route.params.id,
      showAlertError: false,
      showAlertSuccess: false,
      serverErrorsMassage: '',
      isLaoding: false,
      resetSvg: RESETICON,
      passwordSvg: PASSWORD,
      okSvg: OK
    }
  },

  created () {
    window.scrollTo(0, 0)
  },

  methods: {
    resetPassword () {
      this.isLaoding = true
      this.$refs.recaptcha.execute()
    },

    onCaptchaVerified (recaptchaToken) {
      const self = this
      self.status = "submitting"
      self.$refs.recaptcha.reset()
      this.$apollo.mutate({
        mutation: RESET_VERIFY,
        fetchPolicy: 'no-cache',
        variables: {
          verifyToken: this.verifyToken,
          password: this.password,
          recaptchaToken: recaptchaToken
        }
      }).then((data) => {
        if (data.data.resetVerify.status) {
          this.isLaoding = false
          window.scrollTo(0, 0)
          this.showAlertError = false
          this.showAlertSuccess = true
          setTimeout(() => this.$router.push({ path: '/login' }), 3000)
        }
        else if (!data.data.resetVerify.status) {
          this.isLaoding = false
          window.scrollTo(0, 0)
          this.showAlertError = true
          this.showAlertSuccess = false
          this.serverErrorsMassage = data.data.resetVerify.massage
        }
      }).catch((error) => {
        console.log(error)
      })
    },
  }
}
</script>

<style>
button:disabled,
button[disabled]{
  cursor:not-allowed !important;
}
</style>
