<template>
  <div class="action">

    <navbar class='appNavbar' />

    <!-- content -->
    <div class="actionDiv">

      <!-- SignUp Scucceful massage -->
      <alertSuccess v-if="showAlertSuccess"
      title= "    ..."
      content= "             "
      moreInfo= '    5       
         ()    '
       />

      <alertError v-if="showAlertError"
      title= "    ..."
      content= "      :"
      :moreInfo= "serverErrorsMassage"
      />

      <!-- create account -->
      <div class="actionContainer">

        <!-- title -->
        <h1 class="actionTitle">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="actionSvg">
            <path :d="createSvg" />
          </svg> </h1>

          <!-- info -->
          <ValidationObserver v-slot="{ invalid }" class="w-full">
            <form @submit.prevent="signup" class="form">

              <!-- username -->
              <ValidationProvider rules="required|min_5:5|max_15:15|english:^[A-Za-z0-9@_]+$|String:[a-zA-Z]"
              v-slot="{ errors }" class="formField">
                <div class="field">
                  <label class="fieldLabel"> </label>
                  <input type="text" class="fieldInput" v-model.trim="username" placeholder="mohammed2000"/>
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="fieldSvg">
                    <path :d="userSvg"/></svg>
                </div>
                <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </ValidationProvider>

              <!-- email -->
              <ValidationProvider rules="required|email" v-slot="{ errors }" class="formField">
                <div class="field">
                  <label class="fieldLabel"></label>
                  <input type="email" class="fieldInput" v-model.trim="email" placeholder="mohammed@gmail.com"/>
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                      <path :d="emailSvg"/>
                  </svg>
                </div>
                <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </ValidationProvider>

              <!-- password -->
              <ValidationObserver class="formField">
                <ValidationProvider rules="required|min_7:7|max_15:15" vid="password" v-slot="{ errors }" class="formField">
                  <div class="field">
                    <label class="fieldLabel"> </label>
                    <input type="password" class="fieldInput" ref="password" v-model.trim="password" placeholder="moh_10_1_2000" />
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                      <path :d="passwordSvg" />
                    </svg>
                  </div>
                  <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
                </ValidationProvider>
                <ValidationProvider rules="required|confirmed:password" v-slot="{ errors }" class="formField">
                  <div class="field">
                    <label class="fieldLabel">  </label>
                    <input type="password" class="fieldInput" v-model.trim="passwordConfirm" placeholder="moh_10_1_2000" />
                      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                        <path :d="passwordSvg"/>
                    </svg>
                  </div>
                  <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
                </ValidationProvider>
              </ValidationObserver>

              <!-- phoneType -->
              <ValidationProvider rules="required" v-slot="{ errors }" class="formField">
                <div class="field">
                  <label class="fieldLabel"> </label>
                  <select class="formSelect" dir="rtl" v-model="phoneType" >
                    <option disabled value="">   </option>
                    <option value="Syriatel" style="color:#021A71"> - Syriatel</option>
                    <option value="MTN" style="color:#021A71">   - MTN</option>
                  </select>
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                      <path :d="selectSvg" />
                  </svg>
                </div>
                <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </ValidationProvider>

              <!-- phone -->
              <ValidationProvider rules="required|phoneNumber:^(09)([0-9]{8})$" v-slot="{ errors }" class="formField">
                <div class="field">
                  <label class="fieldLabel"> </label>
                  <input type="tel" class="fieldInput" v-model.trim="phone" placeholder="09333444555" />
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                      <path :d="phoneSvg" />
                  </svg>
                </div>
                <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </ValidationProvider>

              <div class="formNote">
                <router-link :to="{ name: 'send' }" class="formNoteText hover:text-white"
                >      !   </router-link>

                <router-link :to="{ name: 'login' }" class="formNoteText hover:text-white"
                >        </router-link>

                <p class="formNotePara">      <br />
                   </p>
              </div>

              <vue-recaptcha sitekey="6LfsVsQZAAAAAM-_U-Zxk0GwROnmx07HhycfsQgh" ref="recaptcha"
              size="invisible"  @verify="onCaptchaVerified" @expired="onCaptchaExpired" :loadRecaptchaScript="true">
              </vue-recaptcha>

              <button class="formButton" type="submit" :disabled="invalid">
                <svg class="formButtonSvg" viewBox="0 0 58 58" xmlns="http://www.w3.org/2000/svg" v-if="isLaoding">
                  <waitIcon />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48"
                class="formButtonSvg" v-if="!isLaoding">
                    <path :d="okSvg" />
                </svg>
              </button>
            </form>
          </ValidationObserver>

      </div>
    </div>

    <footerDiv />
    <bottomDiv />

  </div>
</template>

<script>
import VueRecaptcha from 'vue-recaptcha';
import navbar from '@/components/navbar.vue'
import waitIcon from '@/components/waitIcon.vue'
import footerDiv from '@/components/footerDiv.vue'
import bottomDiv from '@/components/bottomDiv.vue'
import alertError from '@/components/alertError.vue'
import alertSuccess from '@/components/alertSuccess.vue'
import { ValidationProvider, ValidationObserver, extend, setInteractionMode } from 'vee-validate'
import { required, min, max, regex, email, confirmed } from 'vee-validate/dist/rules'
import { SIGN_UP } from '@/assets/graphql/auth'
import { CREATE, USER, EMAIL, PASSWORD, SELECT, PHONE, OK } from '@/assets/icons.js'

setInteractionMode('aggressive')

extend('required', { ...required, message: '  ' })
extend('min_5', { ...min, message: '       5' })
extend('min_7', { ...min, message: '       7' })
extend('max_15', { ...max, message: '       15' })
extend('email', { ...email, message: '    ' })
extend('confirmed', { ...confirmed, message: '   ' })
extend('phoneNumber', { ...regex, message: '     10    09' })
extend('String', {
  ...regex,
  message: '        '
})
extend('english', {
  ...regex,
  message: '         _  @ '
})

export default {
  components: { VueRecaptcha , waitIcon, ValidationProvider, ValidationObserver, alertError,
    alertSuccess, navbar, footerDiv, bottomDiv },

  data () {
    return {
      username: '',
      email: '',
      password: '',
      passwordConfirm: '',
      phoneType: '',
      phone: '',
      showAlertError: false,
      showAlertSuccess: false,
      serverErrorsMassage: '',
      isLaoding: false,
      createSvg: CREATE,
      userSvg: USER,
      emailSvg: EMAIL,
      passwordSvg: PASSWORD,
      selectSvg: SELECT,
      phoneSvg: PHONE,
      okSvg: OK
    }
  },

  created () {
    window.scrollTo(0, 0)
  },

  methods: {
    async signup () {
      this.isLaoding = true
      this.$refs.recaptcha.execute()
    },

    onCaptchaVerified (recaptchaToken) {
      const signedUp = localStorage.getItem('signedUp')
      if (signedUp) {
        window.scrollTo(0, 0)
        this.serverErrorsMassage =  '        ,     '
        this.isLaoding = false
        this.showAlertError = true
        this.showAlertSuccess = false
        this.serverErrors = true
      } else {
        const self = this
        self.status = "submitting"
        self.$refs.recaptcha.reset()
        this.$apollo.mutate({
          mutation: SIGN_UP,
          fetchPolicy: 'no-cache',
          variables: {
            email: this.email,
            username: this.username,
            password: this.password,
            phoneType: this.phoneType,
            phone: this.phone,
            recaptchaToken: recaptchaToken
          }
        }).then((data) => {
          if (data.data.signUp.status) {
            this.isLaoding = false
            window.scrollTo(0, 0)
            this.showAlertError = false
            this.showAlertSuccess = true
            localStorage.setItem('signedUp', true)
          }
          else if (!data.data.signUp.status) {
            window.scrollTo(0, 0)
            this.serverErrorsMassage = data.data.signUp.massage
            this.isLaoding = false
            this.showAlertError = true
            this.showAlertSuccess = false
            this.serverErrors = true
          }
        }).catch((error) => {
          console.log(error)
        })
      }
    },

    onCaptchaExpired () {
      this.$refs.recaptcha.reset();
    }
  }
}
</script>

<style>
button:disabled,
button[disabled]{
  cursor:not-allowed !important;
}
</style>
