import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import Term from '@/views/term.vue'
import Login from '@/views/login.vue'
import Signup from '@/views/signup.vue'
import Send from '@/views/send.vue'
import SendID from '@/views/sendID.vue'
import Reset from '@/views/reset.vue'
import ResetID from '@/views/resetID.vue'
import Profile from '@/views/profile.vue'
import Work from '@/views/work.vue'
import WorkID from '@/views/workID.vue'
import NotFound from '@/views/notFound.vue'

Vue.use(VueRouter)

const ifNotAuthenticated = (to, from, next) => {
  const token = localStorage.getItem('csrf')
  if (!token) {
    next()
    return
  }
  next('/profile')
}

const ifAuthenticated = (to, from, next) => {
  const token = localStorage.getItem('csrf')
  if (token) {
    next()
    return
  }
  next('/login')
}

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/term',
    name: 'term',
    component: Term,
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    beforeEnter: ifNotAuthenticated
  },
  {
    path: '/signup',
    name: 'signup',
    component: Signup,
    beforeEnter: ifNotAuthenticated
  },
  {
    path: '/send',
    name: 'send',
    component: Send,
    beforeEnter: ifNotAuthenticated
  },
  {
    path: '/send/:id',
    name: 'sendID',
    component: SendID,
    beforeEnter: ifNotAuthenticated
  },
  {
    path: '/reset',
    name: 'reset',
    component: Reset,
    beforeEnter: ifNotAuthenticated
  },
  {
    path: '/reset/:id',
    name: 'resetID',
    component: ResetID,
    beforeEnter: ifNotAuthenticated
  },
  {
    path: '/profile',
    name: 'profile',
    component: Profile,
    beforeEnter: ifAuthenticated
  },
  {
    path: '/work',
    name: 'work',
    component: Work,
    beforeEnter: ifAuthenticated
  },
  {
    path: '/work/:id',
    name: 'workID',
    component: WorkID,
    beforeEnter: ifAuthenticated
  },
  {
    path: '*',
    name: 'notFound',
    component: NotFound
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
