import { gql } from '@apollo/client/core'

export const SIGN_IN = gql`
  query signIn($username: String!, $password: String!, $recaptchaToken: String!) {
    signIn(username: $username, password: $password, recaptchaToken: $recaptchaToken) {
      ... on Error {
        status
        massage
        code
      }
      ... on User {
        status
        token
        username
        money
        email
        phoneType
        phone
        totalProfit
        totalUsers
        siteAge
        withdraws {
          withdrawID
          withdrawDate
          withdrawType
          withdrawSize
          isWithdrawWaiting
          isWithdrawSuccess
          isWithdrawVerified
        }
        work {
          id
          name
          timeToFinish
          paid
          isOpen
          priority
          tags{
            id
            massage
          }
          requirements {
            id
            massage
          }
          steps {
            id
            massage
          }
          explainLink
          proofs{
            id
            massage
            userProof
          }
          finisedTopNum
          createdAt
          userFeed {
            userId
            userProofs {
              id
              proof
            }
            createdAt
            updatedAt
            waitToVerify
            success
            failed
            faildMassage
          }
        }
      }
    }
  }
`

export const RESET = gql`
  query reset($email: String!, $recaptchaToken: String!) {
    reset(email:$email, recaptchaToken: $recaptchaToken) {
      ...on Error {
        massage
        code
        status
      }
      ...on Massage {
        status
      }
    }
  }
`

export const EMAIL_SEND = gql`
  query emailSend($email: String!, $recaptchaToken: String!){
    emailSend(email: $email, recaptchaToken: $recaptchaToken) {
      ...on Error {
        massage
        code
        status
      }
      ...on Massage {
        status
      }
    }
  }
`

export const SIGN_UP = gql`
  mutation signUp($email: String!, $username: String!, $password: String!,
    $phoneType: String!, $phone: String!, $recaptchaToken: String! ) {
    signUp(input: {email: $email, username: $username, password: $password,
      phoneType: $phoneType, phone: $phone, recaptchaToken: $recaptchaToken }) {
        ...on Error {
          massage
          code
          status
        }
        ...on Massage {
          status
        }
    }
  }
`

export const RESET_VERIFY = gql`
  mutation resetVerify($verifyToken: String!, $password: String!, $recaptchaToken: String!) {
    resetVerify(verifyToken: $verifyToken, password: $password, recaptchaToken: $recaptchaToken) {
      ...on Error {
        massage
        code
        status
      }
      ...on Massage {
        status
      }
    }
  }
`

export const EMAIL_VERIFY = gql`
mutation emailVerfiy($verifyToken: String!) {
  emailVerfiy(verifyToken: $verifyToken) {
    ...on Error {
      massage
      code
      status
    }
    ...on Massage {
      status
    }
  }
}
`
