import Vue from 'vue'
import Vuex from 'vuex'
import router from '@/router/index'
import { SIGN_IN } from '@/assets/graphql/auth'
import { Get_Data, Get_Site_Info, Update_Data, Post_Withdraw, Send_Proof, Get_This_Task } from '@/assets/graphql/data'
import { client as apollo } from '@/main'

Vue.use(Vuex)

export default new Vuex.Store({

  getters: {
    getUser: (state) => state.user,
    getThisTask: (state) => state.task,
    getError: (state) => state.error,
    getPhoneError: (state) => state.phoneError,
    getTotalUsers: (state) => state.totalUsers,
    getTotalProfit: (state) => state.totalProfit,
    getIsAuth: (state) => state.isAuth,
    notFound: (state) => state.notFound
  },

  state: {
    user: null,
    error: null,
    phoneError: null,
    totalUsers: 0,
    totalProfit: 0,
    siteAge: 0,
    isAuth: false,
    notFound: false,
    task: null
  },

  mutations: {
    Set_Site_info(state, data) {
      state.totalUsers = data.totalUsers
      state.totalProfit = data.totalProfit
    },
    Set_User_info (state, data) {
      state.user = data
      state.totalUsers = data.totalUsers
      state.totalProfit = data.totalProfit
    },
    Set_Task_info(state, data) {
      state.task = data
      state.totalUsers = data.totalUsers
      state.totalProfit = data.totalProfit
    },
    Set_Error (state, data) {
      state.error = data
      state.totalUsers = data.totalUsers
      state.totalProfit = data.totalProfit
    },
    Set_Phone_Error(state, data) {
      state.phoneError = data
      state.totalUsers = data.totalUsers
      state.totalProfit = data.totalProfit
    },
    Remove_Phone_Error(state) {
      state.phoneError = ''
    },
    Set_Auth (state) {
      if (localStorage.getItem('csrf')) {
        state.isAuth = true
      } else {
        state.isAuth = false
      }
    },
    showNotFound (state) {
      state.notFound = true
    },
    hideNotFound (state) {
      state.notFound = false
    }
  },

  actions: {

    async getSiteInfo ({commit}) {
      try {
        const siteInfo = await apollo.query({ query: Get_Site_Info, fetchPolicy: 'network-only'})
        if (siteInfo.data.getSiteInfo.status) {
          await commit('Set_Site_info', siteInfo.data.getSiteInfo)
        }
      } catch (error) {
        // console.log(error)
      }
    },

    async signin ({commit}, data ) {
      try {
        const userInfo = await apollo.query({ query: SIGN_IN, fetchPolicy: 'network-only',
        variables: { username: data.username, password: data.password, recaptchaToken: data.recaptchaToken }})
        if (userInfo.data.signIn.status) { await commit('Set_User_info', userInfo.data.signIn) } // success
        else if (!userInfo.data.signIn.status) { await commit('Set_Error', userInfo.data.signIn.massage) } //error
      } catch (error) {
        // console.log();
      }
    },

    async getData ({commit}) {
      try {
        const userInfo = await apollo.query({ query: Get_Data, fetchPolicy: 'network-only'})
        if (userInfo.data.getData.status) { await commit('Set_User_info', userInfo.data.getData) }
        else if (!userInfo.data.getData.status && userInfo.data.getData.code == 404) {
          localStorage.removeItem('csrf')
          router.push({ name: 'login', path: '/login' })
        }
      } catch (error) {
        // console.log();
      }
    },

    async updateData ({commit}, data) {
      try {
        const userInfo = await apollo.mutate({ mutation: Update_Data, fetchPolicy: 'no-cache',
        variables: { phone: data.phone, phoneType: data.phoneType }})
        if (userInfo.data.updateData.status) {
          await commit('Set_User_info', userInfo.data.updateData)
          await commit('Remove_Phone_Error')
        }
        else if (!userInfo.data.updateData.status && userInfo.data.updateData.code != 404) {
          await commit('Set_Phone_Error', userInfo.data.updateData.massage)
        }
        else if (!userInfo.data.updateData.status && userInfo.data.updateData.code == 404) {
          localStorage.removeItem('csrf')
          router.push({ name: 'login', path: '/login' })
        }
      } catch (error) {
        // console.log();
      }
    },

    async postWithdraw({commit}, data) {
      try {
        const userInfo = await apollo.mutate({ mutation: Post_Withdraw, fetchPolicy: 'no-cache',
        variables: { withdrawType: data.withdrawType}})
        if (userInfo.data.postWithdraw.status) { await commit('Set_User_info', userInfo.data.postWithdraw) }
        else if (!userInfo.data.postWithdraw.status && userInfo.data.postWithdraw.code == 404) {
          localStorage.removeItem('csrf')
          router.push({ name: 'login', path: '/login' })
        }
      } catch (error) {
        // console.log();
      }
    },

    async sendProof({commit}, data) {
      try {
        const proof = await apollo.mutate({ mutation: Send_Proof, fetchPolicy: 'no-cache',
        variables: { workID: data.workID, data: data.proof }})
        console.log(proof)
        if (proof.data.sendProof.status) {
          await commit('Set_User_info', proof.data.sendProof)
          router.push({ name: 'work', path: '/work' })
        }
        else if (!proof.data.sendProof.status && proof.data.sendProof.code == 404) {
          localStorage.removeItem('csrf')
          router.push({ name: 'login', path: '/login' })
        }
        await router.push({ name: 'work', path: '/work' })
      } catch (error) {
        // console.log();
      }
    },

    async getThisTask({commit}, data) {
      try {
        const thisTask = await apollo.query({ query: Get_This_Task, fetchPolicy: 'network-only',
        variables: { workID: data.workID }})
        if (thisTask.data.getThisTask.status) { await commit('Set_Task_info', thisTask.data.getThisTask) }
        else if (!thisTask.data.getThisTask.status && thisTask.data.getThisTask.code == 404) {
          localStorage.removeItem('csrf')
          router.push({ name: 'login', path: '/login' })
        }
      } catch (error) {
        // console.log();
      }
    }

  }
})
