<template>
  <div class="profile">

    <div class="profile">

      <navbar class='appNavbar' />

      <!-- header -->
      <div class="profileHeader">
        <div class="profileHeaderDiv">
          <div class="profileContainer">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="profileSvg">
              <path :d="userSvg"/>
            </svg>
            <h1 v-if="!isPageLoaded">  : {{ username }}</h1>
            <wait v-if="isPageLoaded" width="w-15vp"/>
          </div>
          <div class="profileContainer">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileSvg">
              <path :d="emailSvg"/>
            </svg>
            <h1 v-if="!isPageLoaded"> : {{ email }}</h1>
            <wait v-if="isPageLoaded" width="w-15vp"/>
          </div>
          <div class="profileContainer">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileSvg">
              <path :d="moneySvg" />
            </svg>
            <h1 v-if="!isPageLoaded"> : {{ money }} .</h1>
            <wait v-if="isPageLoaded" width="w-15vp"/>
          </div>
        </div>
        <img src="@/assets/images/profile.png" class="profileImg" alt="" />
      </div>

      <!-- content ONE -->
      <div class="profileContent">

        <!-- personal information -->
        <div class="profileInfo">

          <!-- title -->
          <div class="profileInfoTitle">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileInfoTitleSvg">
              <path :d="personalSvg" />
            </svg>
            <h1> </h1>
          </div>

          <div class="profileInfoContent">
            <div class="profileInfoDiv" v-for="content in personalInfo" :key="content.id">
              <svg xmlns="http://www.w3.org/2000/svg" :viewBox="content.viewBox" class="profileInfoSvg">
                <path :d=content.path />
              </svg>
              <h3 v-if="!isPageLoaded">{{ content.text }}</h3>
              <wait v-if="isPageLoaded" width="w-15vp"/>
            </div>
          </div>

          <div class="profileButton">
            <button class="profileButtonDiv" @click="edit" :disabled="isPageLoaded">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileInfoSvg">
                <path :d="editSvg" />
              </svg>
              <h1>    </h1>
            </button>
          </div>

        </div>

        <!-- work information -->
        <div class="profileInfo">

          <!-- title -->
          <div class="profileInfoTitle">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileInfoTitleSvg">
              <path :d="workSvg" />
            </svg>
            <h1> </h1>
          </div>

          <div class="profileInfoContent">
            <div class="profileInfoDiv" v-for="content in workInfo" :key="content.id">
              <svg xmlns="http://www.w3.org/2000/svg" :viewBox="content.viewBox" class="profileInfoSvg">
                <path :d=content.path />
              </svg>
              <h3 v-if="!isPageLoaded">{{ content.text }}</h3>
              <wait v-if="isPageLoaded" width="w-15vp"/>
            </div>
          </div>
        </div>
      </div>

      <!-- content Two -->
      <div class="profileContent">

        <!-- money information -->
        <div class="profileInfo">

          <!-- title -->
          <div class="profileInfoTitle">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileInfoTitleSvg">
              <path :d="moneySvg" />
            </svg>
            <h1> </h1>
          </div>

          <div class="profileInfoContent">
            <div class="profileInfoDiv" v-for="content in moneyInfo" :key="content.id">
              <svg xmlns="http://www.w3.org/2000/svg" :viewBox="content.viewBox" class="profileInfoSvg">
                <path :d=content.path />
              </svg>
              <h3 v-if="!isPageLoaded">{{ content.text }}</h3>
              <wait v-if="isPageLoaded" width="w-15vp"/>
            </div>
          </div>

          <div class="profileButton">
            <button class="profileButtonDiv" @click="confrmWithdraw" :disabled="isPageLoaded">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileInfoSvg">
                <path :d="withdrawSvg" />
              </svg>
              <h1> </h1>
            </button>
          </div>
        </div>

        <!-- site information -->
        <div class="profileInfo">

          <!-- title -->
          <div class="profileInfoTitle">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileInfoTitleSvg">
              <path :d="siteSvg" />
            </svg>
            <h1> </h1>
          </div>

          <div class="profileInfoContent">
            <div class="profileInfoDiv" v-for="content in siteInfo" :key="content.id">
              <svg xmlns="http://www.w3.org/2000/svg" :viewBox="content.viewBox" class="profileInfoSvg">
                <path :d=content.path />
              </svg>
              <h3 v-if="!isPageLoaded">{{ content.text }}</h3>
              <wait v-if="isPageLoaded" width="w-15vp"/>
            </div>
          </div>
        </div>
      </div>

      <div class="profileTable" v-if="withdraws.length > 0">
        <table class="table-auto">
          <thead>
            <tr class="profileTableTR">
              <th class="profileTableTH">  </th>
              <th class="profileTableTH"> </th>
              <th class="profileTableTH">  </th>
              <th class="profileTableTH"> </th>
              <th class="profileTableTH">  </th>
            </tr>
          </thead>
          <tbody>
            <tr class="text-center" v-for="withdraw in withdraws" :key="withdraw.withdrawID">
              <td class="profileTableTD">{{ new Date(withdraw.withdrawDate * 1).toLocaleDateString("en-GB") }}</td>
              <td class="profileTableTD">{{ withdraw.withdrawSize }} {{ withdraw.withdrawType }}</td>
              <td class="profileTableTD">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48"
                class="profileTableSvg" v-if="withdraw.isWithdrawWaiting">
                  <path :d="waitSvg" />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"
                class="profileTableSvg" v-if="!withdraw.isWithdrawWaiting">
                  <path :d="completeSvg" />
                </svg>
              </td>
              <td class="profileTableTD">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48"
                class="profileTableSvg" v-if="withdraw.isWithdrawSuccess">
                    <path :d="waitSvg" />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"
                class="profileTableSvg" v-if="!withdraw.isWithdrawSuccess">
                  <path :d="completeSvg" />
                </svg>
              </td>
              <td class="profileTableTD">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48"
                class="profileTableSvg" v-if="withdraw.isWithdrawVerified">
                    <path :d="waitSvg" />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"
                class="profileTableSvg" v-if="!withdraw.isWithdrawVerified">
                  <path :d="completeSvg" />
                </svg>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- edit phone and phoneType -->
      <div class="profileEdit" v-if="editInfo">
        <div class="profileEditContainer">

          <div class="profileEditDiv border-green-500 bg-green-100 text-green-900" v-if="finished">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="profileEditSvg text-green-600">
              <path :d="successSvg"/></svg>
            <p>   </p>
          </div>

          <div class="profileEditDiv border-red-500 bg-red-100 text-red-900" v-if="phoneError">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="profileEditSvg text-red-600">
              <path :d="errorSvg"/></svg>
            <p>   </p>
          </div>

          <h2 class="profileEditTitle">   :</h2>

          <ValidationObserver v-slot="{ invalid }">

            <!-- phoneType -->
            <ValidationProvider rules="required" v-slot="{ errors }" class="formField">
              <div class="field">
                <label class="fieldLabel"> </label>
                <select class="formSelect" dir="rtl" v-model="phoneType" >
                  <option disabled value="">   </option>
                  <option value="Syriatel" style="color:#021A71"> - Syriatel</option>
                  <option value="MTN" style="color:#021A71">   - MTN</option>
                </select>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                    <path :d="selectSvg" />
                </svg>
              </div>
              <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
            </ValidationProvider>

            <!-- phone -->
            <ValidationProvider rules="required|phoneNumber:^(09)([0-9]{8})$" v-slot="{ errors }" class="formField">
              <div class="field">
                <label class="fieldLabel"> </label>
                <input type="tel" class="formSelect" v-model.trim="phone" placeholder="09333444555" />
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                    <path :d="phoneSvg" />
                </svg>
              </div>
              <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
            </ValidationProvider>

            <div class="infoDiv">
              <button class="formButton" type="submit" :disabled="invalid" @click="updateInfo">
                <svg class="formButtonSvg" viewBox="0 0 58 58" xmlns="http://www.w3.org/2000/svg" v-if="isLaoding">
                  <waitIcon />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="formButtonSvg" v-if="!isLaoding">
                  <path :d="okSvg"/>
                </svg>
              </button>

              <button class="formButton" type="submit" @click="edit">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="formButtonSvg">
                  <path :d="completeSvg" />
                </svg>
              </button>
            </div>

          </ValidationObserver>
        </div>
        <div class="profileOuterDic" v-if="editInfo" @click="edit"></div>
      </div>

      <div class="profileEdit" v-if="withdrawDiv">
        <div class="profileEditContainer">

          <div class="profileEditDiv" v-if="finished">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="profileEditSvg text-green-600">
              <path :d="successSvg" /></svg>
            <p>    </p>
          </div>

          <!--  -->

          <h2 class="profileEditTitle" v-if="!isWithdrawActive && canWithdraw">     {{ minimumWithdrawal }} . </h2>
          <h3 class="profileEditText" v-if="!canWithdraw">      </h3>
          <h3 class="profileEditText" v-if="isWithdrawActive"
          >                  </h3>
          <div class="infoDiv" v-if="!isWithdrawActive && canWithdraw">
            <ValidationObserver v-slot="{ invalid }" class="w-full">
              <ValidationProvider rules="required" v-slot="{ errors }" class="formField">
                <div class="field">
                  <label class="fieldLabel"> </label>
                  <select class="formSelect" dir="rtl" v-model="withdrawType" >
                    <option disabled value="">   </option>
                    <option value="layrats" style="color:#021A71"> </option>
                    <option value="megas" style="color:#021A71"> </option>
                  </select>
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                      <path :d="selectSvg" />
                  </svg>
                </div>
                <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </ValidationProvider>
              <div class="footerDiv">
                <button class="formButton" type="submit" :disabled="invalid" @click="postWithdraw">
                  <svg class="formButtonSvg" viewBox="0 0 58 58" xmlns="http://www.w3.org/2000/svg" v-if="isLaoding">
                    <waitIcon />
                  </svg>
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="formButtonSvg" v-if="!isLaoding">
                    <path :d="okSvg" />
                  </svg>
                </button>
                <button class="formButton" type="submit" @click="hideWithdraw">
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="formButtonSvg">
                    <path :d="completeSvg" />
                  </svg>
                </button>
              </div>
            </ValidationObserver>
          </div>
        </div>
        <div class="profileOuterDic" v-if="withdrawDiv" @click="hideWithdraw"></div>
      </div>
      <footerDiv />
      <bottomDiv />
    </div>
  </div>
</template>

<script>
import wait from '@/components/wait.vue'
import navbar from '@/components/navbar.vue'
import waitIcon from '@/components/waitIcon.vue'
import footerDiv from '@/components/footerDiv.vue'
import bottomDiv from '@/components/bottomDiv.vue'
import { ValidationProvider, ValidationObserver, extend, setInteractionMode } from 'vee-validate'
import { required, regex } from 'vee-validate/dist/rules'
import { USERS, USER, OK, EMAIL, PERSONAL, MONEY, EDIT, WORK, WITHDRAW, SITE, WAITING, SHIELD, ERROR, PULSE, LAYERS,
  LEVEL, AGE, TOTAL, ANSWERS, PERCENT, WAIT, COMPLETE, SUCCESS, SELECT, PHONE, ONMONEY } from '@/assets/icons.js'
setInteractionMode('aggressive')
extend('phoneNumber', { ...regex, message: '     10    09' })
extend('required', { ...required, message: '  ' })

export default {

  components: { waitIcon, wait, ValidationProvider, ValidationObserver, navbar, footerDiv, bottomDiv },

  data () {
    return {
      username: '',
      email: '',
      money: 0,
      phoneType: '',
      phone: '',
      totalTasks: 0,
      awaitTasks: 0,
      successTasks: 0,
      failedTasks: 0,
      successPrecentage: 0,
      totalEarning: 0,
      totalWithdraw: '',
      awaitEarning: 0,
      totalUsers: 0,
      totalPaid: 0,
      siteAge: 0,
      minimumWithdrawal: 0,
      availableTasks: 0,
      withdraws: [],
      withdrawType: '',
      isLaoding: false,
      finished: false,
      phoneError: false,

      // button div
      editInfo: false,
      withdrawDiv: false,
      isWithdrawActive: false,
      isPageLoaded: false,
      userSvg: USER,
      okSvg: OK,
      emailSvg: EMAIL,
      personalSvg: PERSONAL,
      moneySvg: MONEY,
      editSvg: EDIT,
      workSvg: WORK,
      withdrawSvg: WITHDRAW,
      siteSvg: SITE,
      waitSvg: WAIT,
      completeSvg: COMPLETE,
      successSvg: SUCCESS,
      errorSvg: ERROR,
      selectSvg: SELECT,
      phoneSvg: PHONE
    };
  },

  created () {
    window.scrollTo(0, 0)
  },

  methods: {
    edit () {
      this.editInfo = !this.editInfo
    },

    async updateInfo () {
      this.isLaoding = true
      await this.$store.dispatch('updateData', { phone:this.phone, phoneType: this.phoneType })
      this.getInfo()
      this.isLaoding = false
      let err = await this.$store.getters['getPhoneError']
      if (err) {
        this.phoneError = true
      } else {
        this.finished = true
      }
      this.$store.commit('Remove_Phone_Error')
      setTimeout(() => this.phoneError = false, 3000)
      setTimeout(() => this.finished = false, 3000)
      setTimeout(() => this.editInfo = !this.editInfo, 3000)
    },

    confrmWithdraw () {
      this.withdrawDiv = !this.withdrawDiv
    },

    hideWithdraw () {
      this.withdrawDiv = false
    },

    async postWithdraw () {
      this.isLaoding = true
      await this.$store.dispatch('postWithdraw', { withdrawType: this.withdrawType })
      await this.getInfo()
      this.isLaoding = false
      this.finished = true
      setTimeout(() => this.finished = false, 3000)
      setTimeout(() => this.withdrawDiv = false, 3000)
    },

    async getInfo () {

      // reset all value
      this.money = ''
      this.phoneType = ''
      this.phone = ''
      this.totalTasks = 0
      this.awaitTasks = 0
      this.successTasks = 0
      this.failedTasks = 0
      this.successPrecentage = 0
      this.totalEarning = 0
      this.totalWithdraw = ''
      this.awaitEarning = 0
      this.totalUsers = 0
      this.totalPaid = 0
      this.siteAge = 0
      this.minimumWithdrawal = 0
      this.availableTasks = 0
      this.withdraws = []

      // get all data
      let data = await this.$store.getters['getUser']
      if (!data) {
        await this.$store.dispatch('getData')
        data = await this.$store.getters['getUser']
      }

      // assign all value
      this.username = data.username
      this.email = data.email
      this.money = data.money
      this.phoneType = data.phoneType
      this.phone = data.phone
      this.money = data.money
      this.withdraws = data.withdraws

      // calculate totalTasks + awaitTasks + awaitEarning + successTasks + totalEarning + failedTasks
      for (let i = 0; i < data.work.length; i++) {
        // culculate availableTasks
        if ( data.work[i].isOpen && data.work[i].userFeed.length == 0 ) {
            this.availableTasks += 1
          }
        if (data.work[i].userFeed.length > 0) {
          this.totalTasks += 1
          for (var k = 0; k < data.work[i].userFeed.length; k++) {
            if (data.work[i].userFeed[k].waitToVerify) {
              this.awaitTasks += 1
              this.awaitEarning += data.work[i].paid
            }
            if (data.work[i].userFeed[k].success) {
              this.successTasks += 1
              this.totalEarning += data.work[i].paid
            }
            if (data.work[i].userFeed[k].failed) {
              this.failedTasks += 1
            }
          }
        }
      }

      // culculate successPrecentage
      if (this.totalTasks > 0) {
        this.successPrecentage = ( this.successTasks * 100 ) / this.totalTasks
        this.successPrecentage = parseInt(this.successPrecentage)
      } else {
        this.successPrecentage = 0
      }

      // culculate totalWithdraw
      this.totalWithdraw = data.withdraws.length

      for (var i = 0; i < data.withdraws.length; i++) {
        if (!data.withdraws[i].isWithdrawVerified) {
          this.isWithdrawActive = true;
          break;
        }
      }

      this.totalUsers = data.totalUsers
      this.totalPaid = data.totalProfit
      this.siteAge = Math.floor((Date.now() - 1601510400 * 1000) / (1000 * 60 * 60 * 24) )

      // culculate minimumWithdrawal
      if ( data.withdraws.length == 0 ) {
        this.minimumWithdrawal = 150
      } else {
        this.minimumWithdrawal = 500
      }
    }
  },

  async mounted () {
    this.isPageLoaded = true
    await this.getInfo()
    this.isPageLoaded = false
  },

  computed: {

    canWithdraw () {
      return this.money >= this.minimumWithdrawal
    },

    // presonal Information
    personalInfo () {
      return [
        { id: 1, viewBox:'0 0 20 20', path: USER, text: `  : ${this.username}` },
        { id: 2, viewBox:'0 0 48 48', path: EMAIL, text: ` : ${this.email}` },
        { id: 3, viewBox:'0 0 48 48', path: SELECT, text: `  : ${this.phoneType}` },
        { id: 4, viewBox:'0 0 48 48', path: PHONE, text: `  : ${this.phone}` }
      ]
    },

    // work Information
    workInfo () {
      return [
        { id: 1, viewBox:'0 0 48 48', path: WAIT, text: `   : ${this.totalTasks}` },
        { id: 2, viewBox:'0 0 48 48', path: WAITING, text: `    : ${this.awaitTasks}` },
        { id: 3, viewBox:'0 0 20 20', path: SHIELD, text: `    : ${this.successTasks}` },
        { id: 4, viewBox:'0 0 48 48', path: ERROR, text: `    : ${this.failedTasks}` },
        { id: 5, viewBox:'0 0 20 20', path: PERCENT, text: `  : ${this.successPrecentage} %` }
      ]
    },

    // money Information
    moneyInfo () {
      return [
        { id: 1, viewBox:'0 0 48 48', path: ANSWERS, text: `  : ${this.totalEarning} .` },
        { id: 2, viewBox:'0 0 48 48', path: TOTAL, text: `   : ${this.totalWithdraw}` },
        { id: 3, viewBox:'0 0 20 20', path: ONMONEY, text: `  : ${this.awaitEarning} .` },
        { id: 4, viewBox:'0 0 20 20', path: PULSE, text: ` : ${this.money} .` }
      ]
    },

    // site Information
    siteInfo () {
      return [
        { id: 1, viewBox: '0 0 48 48', path: USERS, text: `  : ${this.totalUsers}` },
        { id: 2, viewBox:'0 0 48 48', path: MONEY, text: `   : ${this.totalPaid} .` },
        { id: 3, viewBox:'0 0 48 48', path: AGE, text: `  : ${this.siteAge} ` },
        { id: 4, viewBox:'0 0 20 20', path: LEVEL, text: `   : ${this.minimumWithdrawal} .` },
        { id: 5, viewBox:'0 0 20 20', path: LAYERS, text: `   : ${this.availableTasks}` }
      ]
    }
  }
}
</script>

<style>
button:disabled,
button[disabled]{
  cursor:not-allowed !important;
}
</style>
