import { gql } from '@apollo/client/core'

const returnData = `
...on Error {
  massage
  code
  status
}
...on Data{
  status
  username
  money
  email
  phoneType
  phone
  totalProfit
  totalUsers
  siteAge
  withdraws {
    withdrawID
    withdrawDate
    withdrawType
    withdrawSize
    isWithdrawWaiting
    isWithdrawSuccess
    isWithdrawVerified
  }
  work {
    id
    name
    timeToFinish
    paid
    isOpen
    priority
    tags{
      id
      massage
    }
    requirements {
      id
      massage
    }
    steps {
      id
      massage
    }
    explainLink
    proofs{
      id
      massage
      userProof
    }
    finisedTopNum
    createdAt
    userFeed {
      userId
      userProofs {
        id
        proof
      }
      createdAt
      updatedAt
      waitToVerify
      success
      failed
      faildMassage
    }
  }
}
`

export const Get_This_Task = gql`
query getThisTask($workID: ID!){
  getThisTask(workID: $workID) {
    ...on Error {
      massage
      code
      status
    }
    ...on Task {
      status
      id
      name
      timeToFinish
      paid
      isOpen
      priority
      tags{
        id
        massage
      }
      requirements {
        id
        massage
      }
      steps {
        id
        massage
      }
      explainLink
      proofs{
        id
        massage
        userProof
      }
      finisedTopNum
      createdAt
      userFeed {
        userId
        userProofs {
          id
          proof
        }
        createdAt
        updatedAt
        waitToVerify
        success
        failed
        faildMassage
      }
    }
  }
}
`

export const Get_Data = gql`
query {
  getData {
    ${returnData}
  }
}
`

export const Get_Site_Info = gql`
query {
  getSiteInfo {
    status
    totalProfit
    totalUsers
    siteAge
  }
}
`

export const Update_Data = gql`
mutation updateData( $phone: String!, $phoneType: String! ) {
  updateData(phone: $phone, phoneType: $phoneType) {
    ${returnData}
  }
}
`

export const Post_Withdraw = gql`
mutation postWithdraw( $withdrawType: String! ){
  postWithdraw( withdrawType: $withdrawType ) {
    ${returnData}
  }
}
`
export const Send_Proof = gql`
mutation sendProof( $workID: ID , $data: String ) {
  sendProof( workID: $workID, data: $data) {
    ${returnData}
  }
}
`
