<template>
  <div class="action">

    <navbar class='appNavbar' />

    <!-- content -->
    <div class="actionDiv">

      <div class="formNote">
        <!-- SignUp Scucceful massage -->
        <alertSuccess v-if="showAlertSuccess"
        title= "    ..."
        content= "      "
         />

        <alertError v-if="showAlertError"
        title= "    ..."
        :moreInfo= "serverErrorsMassage"
        send= "    "
        />
      </div>

    </div>

    <footerDiv />
    <bottomDiv />
  </div>
</template>

<script>
import navbar from '@/components/navbar.vue'
import footerDiv from '@/components/footerDiv.vue'
import bottomDiv from '@/components/bottomDiv.vue'
import alertError from '@/components/alertError.vue'
import alertSuccess from '@/components/alertSuccess.vue'
import { EMAIL_VERIFY } from '@/assets/graphql/auth'

export default {
  components: { alertError, alertSuccess, navbar, footerDiv, bottomDiv },

  data () {
    return {
      showAlertError: false,
      showAlertSuccess: false,
      serverErrorsMassage: ''
    }
  },

  mounted () {
    window.scrollTo(0, 0)
    this.isLaoding = true
    this.$apollo.mutate({
      mutation: EMAIL_VERIFY, fetchPolicy: 'no-cache',
      variables: { verifyToken: this.$route.params.id }
    }).then((data) => {
      if (data.data.emailVerfiy.status) {
        this.isLaoding = false
        window.scrollTo(0, 0)
        this.showAlertError = false
        this.showAlertSuccess = true
        setTimeout(() => this.$router.push({ path: '/login' }), 3000)
      }
      else if (!data.data.emailVerfiy.status) {
        this.isLaoding = false
        window.scrollTo(0, 0)
        this.showAlertError = true
        this.showAlertSuccess = false
        this.serverErrorsMassage = data.data.emailVerfiy.massage
      }
    }).catch((error) => {
      console.log(error)
    })
  }
}
</script>
