<template>
  <div class="action">
    <navbar class='appNavbar' />

    <!-- content -->
    <div class="actionDiv">

      <!-- SignUp Scucceful massage -->
      <alertSuccess v-if="showAlertSuccess"
      title= "   ..."
      content= "     "
       />

      <alertError v-if="showAlertError"
      title= "    ..."
      content= "      :"
      :moreInfo= "serverErrorsMassage"
      />

      <!-- login to your account -->
      <div class="actionContainer">

        <!-- title -->
        <h1 class="actionTitle">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="actionSvg">
            <path :d="userSvg"/>
          </svg> </h1>

          <!-- info -->
          <ValidationObserver v-slot="{ invalid }" class="w-full">
            <form @submit.prevent="signin" class="form">

              <!-- username -->
              <ValidationProvider rules="required|min_5:5|max_15:15|english:^[A-Za-z0-9@_]+$|String:[a-zA-Z]"
              v-slot="{ errors }" class="formField">
                <div class="field">
                  <label class="fieldLabel"> </label>
                  <input type="text" class="fieldInput" v-model.trim="username" placeholder="mohammed2000"/>
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="fieldSvg">
                    <path :d="userSvg"/>
                  </svg>
                </div>
                <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </ValidationProvider>

              <!-- password -->
              <ValidationProvider rules="required|required|min_7:7|max_15:15"
              v-slot="{ errors }" class="formField">
                <div class="field">
                  <label class="fieldLabel"> </label>
                  <input type="password" class="fieldInput" v-model.trim="password" placeholder="moh_10_1_2000" />
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="fieldSvg">
                    <path :d="passwordSvg"/>
                  </svg>
                </div>
                <span class="fieldSpan" v-if="errors[0]">{{ errors[0] }}</span>
              </ValidationProvider>

              <vue-recaptcha sitekey="6LfsVsQZAAAAAM-_U-Zxk0GwROnmx07HhycfsQgh" ref="recaptcha"
              size="invisible"  @verify="onCaptchaVerified" @expired="onCaptchaExpired" :loadRecaptchaScript="true">
              </vue-recaptcha>

              <div class="formNote">
                <router-link :to="{ name: 'reset' }" class="formNoteText hover:text-white"
                >    !</router-link>

                <router-link :to="{ name: 'send' }" class="formNoteText hover:text-white"
                >      !   </router-link>

                <router-link :to="{ name: 'signup' }" class="formNoteText hover:text-white"
                >       </router-link>
              </div>

              <button class="formButton" type="submit" :disabled="invalid">
                <svg class="formButtonSvg" viewBox="0 0 58 58" xmlns="http://www.w3.org/2000/svg" v-if="isLaoding">
                  <waitIcon />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="formButtonSvg"  v-if="!isLaoding">
                  <path :d="okSvg" />
                </svg>
              </button>
            </form>
          </ValidationObserver>
      </div>
    </div>

    <footerDiv />
    <bottomDiv />

  </div>
</template>

<script>
import VueRecaptcha from 'vue-recaptcha';

import navbar from '@/components/navbar.vue'
import waitIcon from '@/components/waitIcon.vue'
import footerDiv from '@/components/footerDiv.vue'
import bottomDiv from '@/components/bottomDiv.vue'
import alertError from '@/components/alertError.vue'
import alertSuccess from '@/components/alertSuccess.vue'
import { ValidationProvider, ValidationObserver, extend, setInteractionMode } from 'vee-validate'
import { required, min, max, regex } from 'vee-validate/dist/rules'
import { USER, PASSWORD, OK } from '@/assets/icons.js'

setInteractionMode('aggressive')

extend('required', { ...required, message: '  ' })
extend('min_5', { ...min, message: '       5' })
extend('min_7', { ...min, message: '       7' })
extend('max_15', { ...max, message: '       15' })
extend('String', {
  ...regex,
  message: '        '
})
extend('english', {
  ...regex,
  message: '         _  @ '
})

export default {

  components: { VueRecaptcha ,waitIcon, ValidationProvider, ValidationObserver,
    alertError, alertSuccess, navbar, footerDiv, bottomDiv },

  data () {
    return {
      username: '',
      password: '',
      showAlertError: false,
      showAlertSuccess: false,
      serverErrorsMassage: '',
      isLaoding: false,
      userSvg: USER,
      passwordSvg: PASSWORD,
      okSvg: OK
    }
  },

  created () {
    window.scrollTo(0, 0)
  },

  methods: {
    async signin () {
      this.isLaoding = true
      this.$refs.recaptcha.execute()
    },

    async onCaptchaVerified (recaptchaToken) {
      const self = this
      self.status = "submitting"
      self.$refs.recaptcha.reset()
      await this.$store.dispatch('signin', { username:this.username, password:this.password, recaptchaToken: recaptchaToken })
      const data = await this.$store.getters['getUser']
      if (data && data.status) {
        this.isLaoding = false
        window.scrollTo(0, 0)
        this.showAlertError = false
        this.showAlertSuccess = true
        await localStorage.setItem('csrf', data.token)
        this.$store.commit('Set_Auth')
        setTimeout(() => this.$router.push({ name: 'profile', path: '/profile' }), 3000)
        location.reload()
      } else {
        this.isLaoding = false
        const err = await this.$store.getters['getError']
        this.serverErrorsMassage = err
        this.$store.commit('Set_Error', null)
        window.scrollTo(0, 0)
        this.showAlertError = true
        this.showAlertSuccess = false
      }
    },

    async onCaptchaExpired () {
      this.$refs.recaptcha.reset();
    }

  }

}
</script>

<style>
button:disabled,
button[disabled]{
  cursor:not-allowed !important;
}
</style>
