              <path :d="moneySvg" />
          </svg>
          <h2 v-if="!isPageLoaded">  : {{ totalEarning }} .</h2>
          <wait v-if="isPageLoaded" width="w-15vp"/>
        </div>
        <div class="infoDiv">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workSvg">
            <path :d="waitingSvg" />
          </svg>
          <h2 v-if="!isPageLoaded">  : {{ awaitEarning }} .</h2>
          <wait v-if="isPageLoaded" width="w-15vp"/>
        </div>
        <div class="infoDiv">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workSvg">
            <path :d="withdrawSvg" />
          </svg>
          <h2 v-if="!isPageLoaded"> : {{ money }} .</h2>
          <wait v-if="isPageLoaded" width="w-15vp"/>
        </div>
      </div>

      <!-- availabe tasks title -->
      <div class="workInfo bg-indigo-100 border-indigo-500 text-indigo-900">
        <h2> </h2>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workInfoSvg">
          <path :d="layersSvg"/>
        </svg>
      </div>

      <div class="workInfoNote" v-if="availableTasks.length < 1">
        <p>    </p>
        <p>        </p>
      </div>

      <!-- availabe tasks content -->
      <div class="workContainer">
        <div class="workContent" v-for="task in availableTasks" :key="task.id" @click="goToTask(task.id)">
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workInfoSvg ml-1 transform -rotate-45">
              <path :d="idSvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.id }} </h1>
            <wait v-if="isPageLoaded" width="w-15vp"/>
          </div>
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workSvg">
              <path :d="nameSvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.name }} </h1>
            <wait v-if="isPageLoaded" width="w-15vp"/>
          </div>
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workSvg">
              <path :d="ageSvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.timeToFinish }}  </h1>
            <wait v-if="isPageLoaded" width="w-15vp" />
          </div>
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workSvg">
              <path :d="moneySvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.paid }}  .</h1>
            <wait v-if="isPageLoaded" width="w-15vp" />
          </div>
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workSvg">
              <path :d="requiredSvg" />
            </svg>
            <h1>  :</h1>
            <div class="workTag" v-for="tag in task.tags" :key="tag.id">
              <h1 class="workTagText"> {{ tag.massage }} </h1>
            </div>
          </div>
        </div>
      </div>

      <!-- awaiting tasks title -->
      <div class="workInfo bg-yellow-100 border-yellow-500 text-yellow-900" v-if="awaitTasks.length > 0">
        <h2>   </h2>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workInfoSvg">
          <path :d="waitingSvg"/>
        </svg>
      </div>

      <!-- awaiting tasks content -->
      <div class="workContainer">
        <div class="workContent" v-for="task in awaitTasks" :key="task.id" @click="goToTask(task.id)">
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workSvg">
              <path :d="nameSvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.name }} </h1>
            <wait v-if="isPageLoaded" width="w-15vp"/>
          </div>
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workSvg">
              <path :d="moneySvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.paid }}  .</h1>
            <wait v-if="isPageLoaded" width="w-15vp" />
          </div>
        </div>
      </div>

      <!-- completed tasks title -->
      <div class="workInfo bg-green-100 border-green-500 text-green-900"  v-if="successTasks.length > 0">
        <h2>   </h2>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workInfoSvg">
          <path :d="successSvg"/>
        </svg>
      </div>

      <!-- completed tasks content -->
      <div class="workContainer">
        <div class="workContent" v-for="task in successTasks" :key="task.id" @click="goToTask(task.id)">
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workSvg">
              <path :d="nameSvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.name }} </h1>
            <wait v-if="isPageLoaded" width="w-15vp"/>
          </div>
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workSvg">
              <path :d="moneySvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.paid }}  .</h1>
            <wait v-if="isPageLoaded" width="w-15vp" />
          </div>
        </div>
      </div>

      <!-- failed tasks title -->
      <div class="workInfo bg-red-100 border-red-500 text-red-900" v-if="failedTasks.length > 0">
        <h2>   </h2>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workInfoSvg">
          <path :d="completeSvg" />
        </svg>
      </div>

      <!-- failed tasks content -->
      <div class="workContainer">
        <div class="workContent" v-for="task in failedTasks" :key="task.id" @click="goToTask(task.id)">
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" class="workSvg">
              <path :d="nameSvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.name }} </h1>
            <wait v-if="isPageLoaded" width="w-15vp"/>
          </div>
          <div class="workContentDiv">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" class="workSvg">
              <path :d="moneySvg" />
            </svg>
            <h1>  :</h1>
            <h1 class="workText" v-if="!isPageLoaded"> {{ task.paid }}  .</h1>
            <wait v-if="isPageLoaded" width="w-15vp" />
          </div>
        </div>
      </div>
    </div>

    <footerDiv />
    <bottomDiv />

  </div>
</template>

<script>
import wait from '@/components/wait.vue'
import navbar from '@/components/navbar.vue'
import footerDiv from '@/components/footerDiv.vue'
import bottomDiv from '@/components/bottomDiv.vue'
import { MONEY, WAITING, WITHDRAW, LAYERS, ID, NAME, AGE, REQUIRED, COMPLETE, SUCCESS } from '@/assets/icons.js'
export default {

  components: {
    wait, navbar, footerDiv, bottomDiv
  },

  data () {
    return {
      totalEarning: 0,
      awaitEarning: 0,
      money: 0,
      availableTasks: [],
      awaitTasks: [],
      successTasks: [],
      failedTasks: [],
      isPageLoaded: false,
      moneySvg: MONEY,
      waitingSvg: WAITING,
      withdrawSvg: WITHDRAW,
      layersSvg: LAYERS,
      idSvg: ID,
      nameSvg: NAME,
      ageSvg: AGE,
      requiredSvg: REQUIRED,
      completeSvg: COMPLETE,
      successSvg: SUCCESS
    }
  },

  created () {
    window.scrollTo(0, 0)
  },

  methods: {
    goToTask (id) {
      this.$router.push({ path: `work/${id}` })
    }
  },

  async mounted () {
    this.isPageLoaded = true
    window.scrollTo(0, 0)
    let data = await this.$store.getters['getUser']
    if (!data) {
      await this.$store.dispatch('getData')
      data = await this.$store.getters['getUser']
    }

    this.money = data.money

    for (let i = 0; i < data.work.length; i++) {
      if ( data.work[i].isOpen && data.work[i].userFeed.length == 0 ) {
        this.availableTasks.push(data.work[i])
      }
      if (data.work[i].userFeed.length > 0) {
        for (var k = 0; k < data.work[i].userFeed.length; k++) {
          if (data.work[i].userFeed[k].waitToVerify) {
            this.awaitTasks.push(data.work[i])
            this.awaitEarning += data.work[i].paid
          }
          if (data.work[i].userFeed[k].success) {
            this.successTasks.push(data.work[i])
            this.totalEarning += data.work[i].paid
          }
          if (data.work[i].userFeed[k].failed) {
            this.failedTasks.push(data.work[i])
          }
        }
      }
    }
    this.isPageLoaded = false
  }
}
</script>
